<template>
    <div class="container" @click="handleGalleryClick">
    	<div class="wrapper">
    		<swiper :options="swiperOption">
   
			    <swiper-slide v-for="(item,index) in imgs" :key="index">
			    	<img class="swiper-img" :src="item"  />
			    </swiper-slide>
			    <div class="swiper-pagination"  slot="pagination"></div>
			  </swiper>
    	</div>
    </div>
</template>

<script>

export default {
  name: 'CommonGallery',  
  props:{
  	imgs:{
  		type:Array,
  		
  	}
  },
  data() {
      return {
        swiperOption: {
          pagination:  '.swiper-pagination',
          paginationType: 'fraction',
          observeParents:true,
          observer:true 
        }
       
      }
    },
    methods:{
    	handleGalleryClick(){
    		this.$emit('close');
    	}
    }

}

</script>


<style lang="stylus" scoped>
.container>>>.swiper-container
	overflow:inherit  //aa????????
.container
	position:fixed    //aa
	top:0
	left:0
	right:0
	bottom:0
	display:flex
	justify-content:center
	align-items:center
	background:#000
	z-index:99
	.wrapper
		height:0
		padding-bottom:68.5%
		width:100%
		.swiper-pagination{
			bottom:-1rem;
			color:#fff
		}
		.swiper-slide
			width:100%
			.swiper-img
				width:100%
		
</style>