<template>
  <div class="icons">
  	<swiper :options="swiperOption">
  		 <swiper-slide v-for="(page,index) of pages" :key="index">
		  	<div class="icon" v-for="item of page" :key="item.id"> 
		  		<img class="icon-img" :src="item.imgUrl" />
		  		<p class="icon-desc">{{item.desc}}</p>
		  	</div>
		  </swiper-slide>
	</swiper>
  	
  </div>

</template>

<script>


export default {
  name: 'HomeIcons',  
  props:{
    list:Array
  },
  data(){
    return{
      swiperOption:{
        autoplay:false
      }
    }
  },
  computed:{
  	pages(){
  		const pages=[]
  		this.list.forEach((item,index)=>{
  			const page=Math.floor(index/8)
  			if(!pages[page]){
	  			pages[page]=[]
	  		}
	  		pages[page].push(item)
  		})
  		return pages;

  	}
  }
  
}
</script>



<style lang="stylus" scoped>
	@import '~@/assets/styles/mixins.styl'
  @import '~@/assets/styles/varibles.styl' 
	.icons >>> .swiper-container{
    width:100%
    padding-bottom:50%
    height:0
  }
	.icons{
    margin-top:.2rem 
  }
		.icon
			width:25%
			height:0
			overflow:hidden
			padding-bottom:25%;
			text-align:center;
			display:inline-block
	    .icon-img
	    	width:70%
	    	margin:.10rem auto

	    .icon-desc{
	    	color:$darkTextColor
	    	ellipsis()
	    }
		    
	
</style>
