<template>
	<div>
		<div class="search">
		 	<input v-model="keyword" 
			 	class="search-input" 
			 	type="text" 
			 	placeholder="输入城市名或拼音" 
		 	/>
		</div>
		<div class="search-content"  ref="search" v-show="keyword">
			<ul>
				<li class="search-item border-bottom" 
					v-for="item of list"
					:key="item.id"
					@click="handleCityClick(item.name)"
				>
					{{item.name}}
				</li>
				<li class="search-item border-bottom" v-show="hasNoData">没有找到匹配数据</li>
			</ul>
		</div>
	</div>
	
    
</template>

<script>
import BScroll from 'better-scroll'
import {mapState,mapMutations} from 'vuex'
export default {
  name: 'CitySearch',
  props: {
    cities: Object
  },
  data () {
    return {
      keyword: '',
      list: [],
      timer: null
    }
  },
  
  computed:{
  	hasNoData(){
  		return !this.list.length
  	}
  },
  

  watch:{
  	keyword(){

  		if(this.timer){
  			clearTimeout(this.timer);
  		}
  		if(!this.keyword){
  			this.list=[]
  			return
  		}

  		this.timer=setTimeout(()=>{
  			
  			const result=[];

  			for(let i in this.cities){
  				this.cities[i].forEach((value) => {
  					if(value.spell.indexOf(this.keyword) > -1 ||
  					   value.name.indexOf(this.keyword) > -1){
  						result.push(value)
  					}
  				})
  			}
  			this.list=result
  			console.log(result)
  			
  		},100)
  		
  	}
  },
  methods:{
  	handleCityClick(city){
  		this.changeCity(city)
  		this.$router.push('./')
  		
  	},
  	...mapMutations(['changeCity']) 
  },
  mounted(){
  	this.scroll=new BScroll(this.$refs.search)

  }
  
  
}
</script>

<style lang="stylus" scoped>
@import '~@/assets/styles/varibles.styl'
.search
	line-hegiht:.72rem
	height:.72rem
	padding:0 .1rem 
	background-color:$bgColor
	.search-input
		box-sizing:border-box
		line-height:.62rem
		 width:100%
		 padding:0 .1rem
		 text-align:center
		 font-size:.32rem
		 color:#666
		 border-radius:.06rem
.search-content
	position:absolute
	overflow:hidden
	top:1.58rem
	left:0
	bottom:0
	right:0
	background:#eee
	z-index:1
	.search-item
		padding-left:.2rem
		line-height:.62rem
		background:#fff
		color:#666

	
</style>
