<template>
	<div>
		<router-link class="header-abs" to="/" tag="div" v-show="showAbs">
			<i class="iconfont header-abs-back">&#xe682;</i>
		</router-link>
		<div class="header-fixed" v-show="!showAbs" :style="opacityStyle">
			
		 	景点详情
		 	<router-link to="/"> 
		 		<i class="iconfont header-fixed-back">&#xe682;</i>
		 	</router-link>
			
			
		</div>
	</div>
	
    
</template>

<script>

export default {
  name: 'DetailHeader',
  data(){
  	return{
  		showAbs:true,
  		opacityStyle:{
  			opacity:0
  		}

  	}
  },
  methods:{
  	handleScroll(){
  		console.log('scroll')
  		const top=document.documentElement.scrollTop
  		if(top>60){
  			let opacity=top/140
  			opacity=opacity>1 ? 1: opacity
  			this.opacityStyle={opacity}
  			this.showAbs=false
  		}else{
  			this.showAbs=true
  		}
  	}
  },
  activated(){
  	window.addEventListener('scroll', this.handleScroll);
  },
  deactivated(){
  	window.removeEventListener('scroll', this.handleScroll);
  }

}


</script>


<style lang="stylus" scoped>
@import '~@/assets/styles/varibles.styl'
	.header-abs
		position:absolute
		top:.2rem
		left:.2rem
		border-radius:.4rem
		background:#000
		color:#fff
		hegiht:.8rem
		line-height:.8rem
		width:.8rem
		text-align:center
		.header-abs-back
			font-size:.4rem
	.header-fixed
		line-height:$headerHeight
		text-align:center
		background:$bgColor
		color:#fff
		font-size:.32rem
		position:fixed
		top:0
		left:0
		right:0
		z-index:2
		
		.header-fixed-back
			position:absolute
			left:0
			top:0
			width:.64rem
			font-size:.4rem
			color:#fff
		
</style>

